export default {
  data() {
    return {
      loading: false,
      rules: {
        ac_name: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        open_state: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        ac_begin_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        ac_end_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        ac_day_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        get_num: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        all_coin: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        level: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      form: {
        ac_name: "",
        ac_type: "",
        open_state: "",
        ac_begin_time: "",
        ac_end_time: "",
        ac_content:{
          open_time:'',
          continue:'',
          pack_num:[],
          all_coin:'',
          vip_range:[],
        }
      },
      keys: "",
      id: "",
      allData: ""
    };
  },
  created() {
    this.initData();
  },
  methods: {
    //添加
    add() {
      // console.log('add');
      this.form.ac_content.vip_range.push([]);
    },

    //删除
    del(index) {
      this.form.ac_content.vip_range = this.form.ac_content.vip_range.filter((item, idx) => {
        return index !== idx;
      });
    },

    //保存和发送配置
    async onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === 1) {
            //put
            // console.log(this.allData);
            let { data } = await this.$http.HallFunConfig.PutActivityNew4({
              keys: this.keys,
              values: JSON.stringify(this.allData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }else{
               this.$message({
                type: "warning",
                message: "保存失败"
              });
            }
          } else if (type === 2) {
            //post
            this.loading = true;
            let { data } = await this.$http.HallFunConfig.PostActivityNew4({
              keys: this.keys,
              values: JSON.stringify(this.allData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            } else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "发送服务器配置失败"
              });
            }
          }
          this.initData();
        } else {
          this.$message({
            type: "warning",
            message: "必填的项不可以为空!"
          });
          return false;
        }
      });
    },

    async initData() {
      let { data } = await this.$http.HallFunConfig.GetActivityNew4({
        key: "activity_new.lua"
      });
      // console.log(data);
      this.keys = data.data[0].sys_key;
      this.id = data.data[0].id;
      let res = data.data[0].sys_val;
      this.allData = JSON.parse(res);
      // console.log(this.keys, this.id, this.allData);
      Object.keys(this.allData).forEach(item => {
        if (this.allData[item].ac_type === "10004") {
          this.form = this.allData[item];
        }
      });
      // console.log(this.form,this.allData);
    }
  }
};



